@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* news previews */

$news-previews-space: $m-section-gap;
$news-previews-space--hd: 40px;
$news-previews-space--lg: 30px;

.news-previews {
	margin-bottom: $bottom-space--lg;

	//+media('<desk-lg')
	//argin-bottom: $bottom-space--ml

	&:last-child {
		margin-bottom: 0; } }

.news-previews__list {
	@include grid-parent($news-previews-space);
	@include clear-spacing;
	margin-bottom: $bottom-space--md;

	//+media('<desk-hd')
	//include grid-parent($news-previews-space--hd)

	//+media('<desk-lg')
	//include grid-parent($news-previews-space--lg)
	//argin-bottom: $bottom-space--ml

	&:last-child {
		margin-bottom: 0; } }

.news-previews__item {

	position: relative;
	@include grid-item(3, $news-previews-space);
	@include font(1.5rem, round-float(18px/15px, 2));
	letter-spacing: normal;

	//+media('<desk-hd')
	//include grid-item-responsive(3, $news-previews-space--hd)

	//+media('<desk-lg')
	//include grid-item-responsive(3, $news-previews-space--lg)

	//+media('<1280px')
	//include grid-item-responsive(2, $news-previews-space--lg)

	//+media('<tab-lg')
	//include grid-item-responsive(3, $news-previews-space--lg)

	//+media('<tab-sm')
	//include grid-item-responsive(2, $news-previews-space--lg)

	//+media('<mob-md')
 }	//include grid-item-responsive(1, $news-previews-space--lg)

.news-previews__inner {
	display: block;
	height: 100%;
	text-decoration: none;
	color: inherit; }

.news-previews__meta {
	margin-bottom: 1rem;
	cursor: default; }

.news-previews__date {
	display: block;
	@include font(14px, round-float(20/14, 2));
	color: $text-color-light; }

.news-previews__content {
	position: relative;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.news-previews__title, .news-previews__text {
	$line-height: round-float(30/24, 2);
	$font-size: 24px;
	@include font-alt-medium;
	@include font($font-size, $line-height);
	letter-spacing: normal;
	color: $link-color;
	margin-bottom: $bottom-space;
	overflow: hidden;
	max-height: #{4 * $font-size * $line-height};

	&:last-child {
		margin-bottom: 0; }

	//.news-previews__inner:hover &
 }	//olor: $text-color

.news-previews__text {
	@include font-normal; }

.news-previews__footer {
	position: relative; }

.btn-read {
	@include display-inline-block-top;
	@include font-alt-medium;
	@include font(12px, round-float(24/16, 2));
	letter-spacing: 0.1em;
	color: $text-color;
	text-transform: uppercase;
	padding-bottom: 2px;
	border-bottom: 2px solid $text-color;

	&:hover {
		color: $base-color;
		border-color: $base-color; } }

/* news previews end */
