@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user options */
$uo-size: 50px;
$uo-space: 30px;
$uo-icon-size: 20px;
$uo-text-color: white;

.user-options {
	position: relative; }

.user-options__list {
	position: relative;
	list-style: none;
	padding: 0;
	@include clear-spacing;
	line-height: 1;
	@include margin-horizontal(-#{round($uo-space/2)}); }

.user-options__item {
	position: relative;
	@include display-inline-block-middle;
	//+padding-horizontal(round($uo-space/2))
	//padding: round($uo-space/2)
	@include font($font-size, 1);
	letter-spacing: normal;
	padding: 0 round($uo-space/2);

	& > a {
		position: relative;
		display: block;
		color: $uo-text-color;
		line-height: 1;
		//  svg
		fill: $uo-text-color;
		text-decoration: none;
		text-align: center;

		svg {
			@include size($uo-icon-size);

			&.svg-close {
				opacity: 0;
				visibility: hidden;
				z-index: auto; }

			&.svg-magnifier {
				z-index: 1; }

			&.svg-glasses {
				@include size(37px, 20px); }

			& ~ span {
				padding-left: 10px; }

			& ~ span.hide-text {
				padding-left: 0; } }

		path {
			fill: inherit; }

		& > span {
			@include display-inline-block-middle; }

		// hover
		//&:hover
		//olor: $link-color
		//ill: $link-color

		&.active {
			.for-close {
				opacity: 1;
				visibility: visible;
				z-index: 1; }

			.for-open {
				opacity: 0;
				visibility: hidden;
				z-index: auto; } } } }

.user-options__item--search {
	& > a {
		position: relative;
		@include size($uo-icon-size);

		svg {
			position: absolute;
			@include size($uo-icon-size);
			@include position(0); } } }

.user-options__item--msg {
	@include font-alt;
	@include font(12px, 14px);
	letter-spacing: 0.1em;
	text-transform: uppercase;
	text-align: center;
	color: $uo-text-color;

	& > a {
		color: $text-color;
		fill: $text-color;
		background-color: white;
		padding: 13px 20px; } }

/* user options */
