@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

body {
	height: 100%;
	min-width: 320px;
	//min-width: 1446px
	position: relative; }

.wrapper {
	margin: 0 auto;
	//min-height: 100%
	//height: auto !important
	//height: 100%
	min-height: 100vh;
	overflow: hidden;
	position: relative; }

.main {
	position: relative;
	clear: both;
	z-index: 4; }

.main__holder {
	width: 100%;
	@include clear; }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	margin: 0 0 0 276px;
	padding: round($page-layout-space) 0 round($page-layout-space*1.5) $page-layout-space;
	min-height: 50px;

	@include media('<desk-sm') {
		padding: round($page-layout-space--lg) 0 round($page-layout-space--lg*1.5) $page-layout-space--lg; }

	.home-page & {
		margin: 0;
		padding: 0; } }

// header
//.header
//osition: relative
//-index: 5

// logo
.logo-wrap {
	$logo-wrap-width: $sidebar-width + $page-layout-space;
	// ===============
	width: $logo-wrap-width;
	min-width: $logo-wrap-width;

	@include media('<desk-sm') {
		$logo-wrap-width--lg: $sidebar-width + $page-layout-space--lg;
		// ===============
		width: $logo-wrap-width--lg;
		min-width: $logo-wrap-width--lg; } }

// sidebar
.sidebar {
	display: inline;
	float: left;
	margin: 0 0 0 -100%;
	width: $sidebar-width;

	@include media('<tab-lg') {
		display: none; } }

.sidebar__holder {
	@include padding-vertical(round($page-layout-space), round($page-layout-space*1.5));

	@include media('<desk-sm') {
		@include padding-vertical(round($page-layout-space--lg), round($page-layout-space--lg*1.5)); } }

// footer
.footer {
	margin: 0 auto;
	position: relative;
	clear: both;
	z-index: 5; }

.max-wrap {
	max-width: $page-layout-width + $page-layout-space*2;

	@include media('<desk-lg') {
		max-width: none; } }

.layout {
	@include padding-horizontal($page-layout-space);
	@include push-auto;

	@include media('<desk-sm') {
		@include padding-horizontal($page-layout-space--lg); } }

//       (   .layout-article)
//    
.layout-article {
	p, ul, ol, h2, h3, h4, h5, h6, hr, .layout-text {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	//     
	//         .layout-article
	.layout-full-width, .layout-wide {

		@include margin-horizontal(-#{$text-layout-space});

		@include media('<tab-lg') {
			@include margin-horizontal(-#{$text-layout-space--md}); }

		@include media('<tab-sm') {
			@include margin-horizontal(-#{$text-layout-space--sm}); } } }

//       () 
.max-wrap-text {
	max-width: #{$text-layout-width + $text-layout-space*2};

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + $text-layout-space--md*2}; }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + $text-layout-space--sm*2}; } }

//      () 
//    
.layout-text {
	max-width: #{$text-layout-width + $text-layout-space*2};
	@include padding-horizontal($text-layout-space);
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + $text-layout-space--md*2};
		@include padding-horizontal($text-layout-space--md); }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + $text-layout-space--sm*2};
		@include padding-horizontal($text-layout-space--sm); } }

//       () 
.max-wrap-flood {
	max-width: #{$flood-layout-width + $text-layout-space*2};

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + $text-layout-space--md*2}; }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + $text-layout-space--sm*2}; } }

//      () 
//    
.layout-flood {
	max-width: #{$flood-layout-width + $text-layout-space*2};
	@include padding-horizontal($text-layout-space);
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + $text-layout-space--md*2};
		@include padding-horizontal($text-layout-space--md); }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + $text-layout-space--sm*2};
		@include padding-horizontal($text-layout-space--sm); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

//     
.layout-wide {
	@include padding-horizontal($text-layout-space);

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space--md); }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space--sm); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

//     
.layout-full-width {

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

/* footer gov (layout) */
.footer-gov {
	@include padding-vertical($page-layout-space);

	@include media('<desk-sm') {
		@include padding-vertical($page-layout-space); } }

/* footer gov (layout) end */

/* footer bottom (layout) */
.footer-bottom {
	@include padding-vertical($page-layout-space);

	@include media('<desk-sm') {
		@include padding-vertical($page-layout-space); } }

/* footer bottom (layout) end */
