@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* popups */

// default popup
.default-popup {
	max-width: $default-popup-width;
	padding: $default-popup-space;
	background-color: $bg-color-alt;
	@include push-auto;

	@include media('<desk-sm') {
		max-width: $default-popup-width--md;
		padding: $default-popup-space--md; }

	@include media('<mob-md') {
		@include padding-horizontal($text-layout-space--sm); }

	.form-buttons {
		text-align: center; } }

// popup title
.default-popup__title {
	@include font-alt-bold;
	margin-bottom: $bottom-space--md;
	text-align: center;

	@include font($font-size-h2, $line-height-h2);

	@include media('<desk-sm') {
		margin-bottom: $bottom-space; }

	@include media('<tab-lg') {
		@include font($font-size-h2--md, $line-height-h2--md); }

	@include media('<mob-lg') {
		@include font($font-size-h2--sm, $line-height-h2--sm); }

	&:last-child {
		margin-bottom: 0; } }

.default-popup__content {
	max-width: 450px;
	@include push-auto; }

// popup button close
button.mfp-close {
	$mfp-close-btn-size: 40px;
	$mfp-close-icon-size: 20px;

	@include size($mfp-close-btn-size);
	fill: white;
	color: white;
	opacity: 1;
	background-color: #000;
	right: auto;
	top: 0;
	left: 100%;
	margin-left: 10px;

	@include media('<tab-lg') {
		left: auto;
		right: 0;
		top: 0;
		margin-left: 0; }

	svg {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size($mfp-close-icon-size);
		margin: -#{round($mfp-close-icon-size/2)} 0 0 -#{round($mfp-close-icon-size/2)};
		pointer-events: none; }

	path {
		fill: inherit; } }

/* popups end */
