/* VENDORS */
/*======================================================*/
/* jFiler */
.upload-file {
	opacity: 0;
}

.jFiler {
	position: relative;
}

.jFiler-input-dragDrop {
	margin: 0;
	cursor: pointer;
	padding: 20px;
	background: #f3f3f3;
	border: 1px dashed #eaeaea;
	transition: none;
}

.no-touchevents .jFiler-input-dragDrop:hover {
	border-color: black;
}

.jFiler-input-dragDrop.dragged .jFiler-input-icon i {
	transform: translate(0, -8px);
}

.jFiler-input-dragDrop .jFiler-input-icon {
	margin-top: 0;
	margin-bottom: 15px;
	text-align: center;
}

.jFiler-input-dragDrop .jFiler-input-icon i {
	display: inline-block;
	vertical-align: top;
	background: url(../img/attach.png) no-repeat;
	background-size: contain;
	width: 60px;
	height: 44px;
	transition-property: background-color, border-color, transform;
	transition-duration: 0.2s;
}

.jFiler-input-dragDrop .jFiler-input-text strong {
	display: block;
	font-family: "Open Sans", sans-serif;
	font-weight: 400;
	font-style: normal;
	font-size: 1.2rem;
	line-height: 2;
	color: #212121;
	text-align: center;
}

.jFiler-items {
	margin: 50px 0 0;
}

.jFiler-items-list, .user-content .jFiler-items-list {
	margin: 1.8rem 0;
}

.jFiler-items-list li, .user-content .jFiler-items-list li {
	padding-left: 0;
}

.jFiler-items-list li::before, .user-content .jFiler-items-list li::before {
	content: none;
}

.jFiler-item {
	margin: 1.8rem 0;
	font-size: 1.2rem;
	line-height: 2;
	color: #212121;
	overflow: hidden;
}

.jFiler-item-icon {
	display: none;
}

.jFiler-item-title {
	display: inline-block;
	vertical-align: top;
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	font-style: normal;
	margin-right: 10px;
}

.jFiler-item-others {
	display: inline-block;
	vertical-align: top;
	color: #b4b4b4;
	position: relative;
}

.jFiler-item-others::after {
	content: '';
	position: absolute;
	left: 100%;
	bottom: 0;
	width: 999px;
	height: 0;
	border-bottom: 1px dotted #eaeaea;
	margin: 0 0 3px;
}

.jFiler-item-others span {
	padding-right: 5px;
}

.jFiler-item-assets {
	position: absolute;
	right: 0;
	bottom: 0;
}

.jFiler-item-assets ul,
.user-content ul .jFiler-item-assets ul {
	margin: 0;
}

.jFiler-item-assets ul li,
.user-content ul .jFiler-item-assets ul li {
	margin: 0;
	padding: 0 0 3px 8px !important;
	background: #f3f3f3;
}

.jFiler-item-trash-action {
	display: block;
	position: relative;
	width: 20px;
	height: 20px;
	z-index: 2;
	cursor: pointer;
	background-color: #212121;
}

.jFiler-item-trash-action::before, .jFiler-item-trash-action::after {
	content: '';
	position: absolute;
	left: 50%;
	top: 50%;
	width: 16px;
	height: 2px;
	border-radius: 2px;
	background: white;
	margin: -1px 0 0 -8px;
	transition: none;
}

.jFiler-item-trash-action::before {
	transform: rotate(-45deg);
}

.jFiler-item-trash-action::after {
	transform: rotate(45deg);
}

.no-touchevents .jFiler-item-trash-action:hover::before,
.no-touchevents .jFiler-item-trash-action:hover::after {
	background-color: #006bbd;
}

/* jFiler end */
/* slick sliders */
.slick-arrow {
	width: 50px;
	height: 50px;
	display: block;
	position: absolute;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	border: 1px solid #000;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;
}

.slick-arrow::before {
	content: "";
	display: block;
	position: absolute;
	width: 50px;
	height: 0;
	border-top: 2px solid #000;
	opacity: 1;
	margin: -1px 0 0;
	will-change: opacity, transform;
	transform: translate(0, 0);
	transform: translate3d(0, 0, 0);
	transition-property: opacity, transform;
	transition-duration: 0.2s;
}

.slick-arrow::after {
	top: 50%;
	content: "";
	position: absolute;
	opacity: 1;
	pointer-events: none;
	width: 14px;
	height: 14px;
	transform: rotate(45deg);
	border: 0 solid #000;
}

.no-touchevents .slick-arrow:hover {
	opacity: 1;
}

.no-touchevents .slick-arrow:hover::before {
	opacity: 0;
}

.slick-arrow.slick-disabled, .no-touchevents .slick-arrow.slick-disabled {
	opacity: 0.25;
	background: none;
	-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
	filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
	cursor: default;
}

.slick-arrow.slick-disabled::before, .no-touchevents .slick-arrow.slick-disabled::before {
	opacity: 1;
}

.slick-prev {
	left: 0;
}

.slick-prev::before {
	margin-left: 20px;
}

.slick-prev::after {
	left: 50%;
	border-width: 0 0 2px 2px;
	margin: -7px 0 0 -4px;
}

.slick-next {
	right: 0;
}

.slick-next::before {
	margin-left: -21px;
}

.slick-next::after {
	right: 50%;
	border-width: 2px 2px 0 0;
	margin: -7px -4px 0 0;
}

.slick-dots {
	margin: 0;
	line-height: 0;
}

.user-content .slick-dots {
	margin: 0;
	max-width: none;
}

.slick-dots li {
	vertical-align: top;
	width: auto;
	height: auto;
	margin: 0;
}

.slick-dots li, .user-content .slick-dots li {
	padding: 4px 10px;
	margin: 0;
}

.slick-dots li::before, .user-content .slick-dots li::before {
	content: none;
}

.slick-dots li button {
	width: 8px;
	height: 8px;
	padding: 0;
	border-radius: 0;
	background: #006bbd;
	position: relative;
	transition-property: background, border, box-shadow, transform;
	transition-duration: 0.6s;
}

.slick-dots li button::before {
	content: '';
	position: absolute;
	left: 1px;
	top: 1px;
	height: 6px;
	width: 6px;
	background: white;
	border-radius: 0;
	opacity: 1;
	transition-property: transform, opacity;
	transition-duration: 0.2s;
}

.slick-dots li.slick-active button::before {
	opacity: 0;
}

/* slick sliders end */
