.simple-accordion > a {
	display: block;
	position: relative;
	padding: 18px 0;
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	font-style: normal;
	color: #212121;
	text-decoration: none;
	border-top: 2px solid #212121;
	transition-property: color;
	transition-duration: 0.2s;
}

.simple-accordion > a:hover {
	color: #006bbd;
}

.simple-accordion > a > i {
	position: absolute;
	right: 0;
	top: 50%;
	width: 16px;
	height: 10px;
	margin-top: -5px;
	backface-visibility: hidden;
}

.simple-accordion > a > i::before, .simple-accordion > a > i::after {
	content: "";
	display: block;
	position: absolute;
	bottom: 0;
	backface-visibility: hidden;
	width: 12px;
	height: 2px;
	background-color: #212121;
	transition-property: background-color;
	transition-duration: 0.2s;
}

.simple-accordion > a > i::before {
	right: 50%;
	transform-origin: bottom right;
	transform: rotate(45deg);
}

.simple-accordion > a > i::after {
	left: 50%;
	transform-origin: bottom left;
	transform: rotate(-45deg);
}

.simple-accordion > a:hover > i::before, .simple-accordion > a:hover > i::after {
	background-color: #006bbd;
}

.simple-accordion > a.is-open i {
	transform: rotate(0.5turn);
}

