	position: absolute;
	right: 0;
	top: 50%;
	margin-top: 0;
	width: 5px;
	height: 2px;
	background-color: white;
}

.lang > a > span::before {
	transform: rotate(-45deg);
}

.lang > a > span::after {
	right: 3px;
	transform: rotate(45deg);
}

