	text-decoration: none;
}

.developer__logo {
	display: inline-block;
	vertical-align: top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	width: 154px;
	height: 20px;
	text-align: left;
	text-indent: -9999px;
}

.developer__logo::before, .developer__logo::after {
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 149px;
	height: 16px;
}

.developer__logo::before {
	content: "";
	display: block;
	position: absolute;
	top: 4px;
	left: 4px;
	background: url("data:image/png;base64,");
	background-size: 2px;
}

.developer__logo::after {
	content: "";
	display: block;
	position: absolute;
	background: #00ef85 url("data:image/png;base64,") no-repeat center center;
	background-size: auto 16px;
}

.no-touchevents a:hover .developer__logo::after {
	transform: translate(0, -1px);
}

/* developer end */
