@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */
.form-title {
	display: block;
	@include font-alt-bold;
	@include font($font-size-h2, $line-height-h2);
	color: $title-color;
	text-align: center;
	margin-bottom: $bottom-space--md; }

.user-form {
	background: white;
	padding: $page-layout-space;
	margin-bottom: $bottom-space;
	border: 1px solid $border-color;

	&:last-child {
		margin-bottom: 0; }

	@include media('<tab-lg') {
		@include padding-vertical($text-layout-space--md); }

	@include media('<mob-lg') {
		@include padding-vertical($text-layout-space--sm); }

	form {
		display: block; }

	.input-wrap {
		margin-bottom: 40px; }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.captcha {
		@include display-inline-block-top;
		@include clear-spacing;
		position: relative; // !      
		z-index: 1; // !      z-index: -1

		@include clear;

		img {
			$margin-right: 20px;
			display: inline-block;
			vertical-align: middle;
			max-width: calc(50%);
			margin-right: $margin-right;

			& + input {
				display: inline-block;
				vertical-align: middle;
				width: 200px; } }

		@include media('<tab-lg') {
			img {
				display: block;
				max-width: none;
				margin-bottom: $bottom-space--sm;

				& + input {
					display: block;
					max-width: none;
					width: 100%; } } } }

	.form-footer {
		@include clear;

		@include media('<mob-lg') {
			padding-top: $bottom-space--sm; } }

	.form-buttons {
		float: left;
		margin-right: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		//.btn-submit
 }		//idth: 200px

	.form-note {
		overflow: hidden;
		min-height: 50px;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--sm, $line-height--sm);
		letter-spacing: normal;

		.form-mark {
			left: auto;
			margin-left: 0; } } }

/* user form end */
