@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* article-heading */
.article-heading {
	display: flex;
	justify-content: space-between;
	align-items: center;
	clear: both;
	margin-bottom: $bottom-space--ml;

	&:last-child {
		margin-bottom: 0; }

	.btn-light {
		@include margin-horizontal(10px); } }

.article-heading__cell {
	flex: 1 1 auto;

	&:last-child {
		//min-width: 400px
		flex-grow: 0;
		flex-basis: auto;

		.pages-nav {
			min-width: 260px; } } }

/* article-footer end */

/* article-footer */
.article-footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	clear: both;
	margin: $bottom-space--md 0;

	&:last-child {
		margin-bottom: 0; } }

/* article-footer end */

/* pages-nav */
.pages-nav {
	margin-bottom: $bottom-space--ml;

	&:last-child {
		margin-bottom: 0; }

	.btn-light {
		@include margin-horizontal(10px); } }

/* pages-nav end */

/* fast-links */
.fast-links {
	display: table;
	table-layout: fixed;
	width: calc(100% + #{$page-layout-space}*2);
	@include margin-horizontal(-$page-layout-space);

	@include media('<desk-sm') {
		width: calc(100% + #{$page-layout-space--lg}*2);
		@include margin-horizontal(-$page-layout-space--lg); } }

.fast-links__item {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	@include font-alt-medium;
	@include font(24px, round-float(30/24, 1));
	border: solid $border-color-light;
	border-width: 0 0 0 1px;

	@include media('<desk-sm') {
		font-size: 20px; }

	&:nth-child(3n+1) {
		border-left: 0; } }

.fast-links__inner {
	$fast-links-icon-size: 30px, 30px; // width, height
	$fast-links-icon-space: 16px;
	display: block;
	text-decoration: none;
	color: $text-color;
	padding: round($page-layout-space*0.875) $page-layout-space;

	@include media('<desk-sm') {
		padding: round($page-layout-space--lg*0.875) $page-layout-space--lg; }

	svg {
		@include display-inline-block-middle;
		@include size(nth($fast-links-icon-size, 1), nth($fast-links-icon-size, 2));
		margin-right: $fast-links-icon-space; }

	path {
		fill: inherit; }

	span {
		@include display-inline-block-middle;
		max-width: calc(100% - #{nth($fast-links-icon-size, 1) + $fast-links-icon-space} - 10px);
		text-align: left; }

	&:hover {
		color: $link-color;
		fill: $link-color; } }

/* fast-links end */

/* m-section */
.m-section {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	@include margin-horizontal(-#{round($m-section-gap/2)});
	//margin-bottom: $bottom-space--lg
	@include padding-vertical(30px);

	//+media('<desk-lg')
 }	//argin-bottom: $bottom-space--md

.m-section__heading {
	padding: 0 round($m-section-gap/2);
	min-width: $m-section-header-size;
	max-width: $m-section-header-size;

	@include media('<desk-sm') {
		padding: 0 round($m-section-gap--lg/2);
		min-width: $m-section-header-size--lg;
		max-width: $m-section-header-size--lg; }

	h2, h3 {
		position: relative;
		@include font-alt-bold;
		@include font(48px, round-float(60/48, 2));
		margin-bottom: 0.5em;
		padding-bottom: 0.5em;

		&::after {
			@include pseudo();
			left: 0;
			bottom: 0;
			@include size(60px, 0);
			border-top: 2px solid #ccc; } }

	.m-section__heading__text {
		@include font(14px, round-float(24/14, 2));
		margin-bottom: $bottom-space;

		&:last-child {
			margin-bottom: 0; } }

	[class*="btn-"] {
		min-width: 194px; } }

.m-section__content {
	$m-section-content-width: calc(100% - #{$m-section-header-size});
	$m-section-content-width--lg: calc(100% - #{$m-section-header-size--lg});
	// ===========
	padding: 0 round($m-section-gap/2);
	min-width: $m-section-content-width;
	max-width: $m-section-content-width;

	@include media('<desk-sm') {
		padding: 0 round($m-section-gap--lg/2);
		min-width: $m-section-content-width--lg;
		max-width: $m-section-content-width--lg; } }

/* m-section end */

/* news */
.m-section--news {
	margin-bottom: -40px; }

/* news end */

/* docs */
$docs-space: $m-section-gap;
$docs-space--hd: 40px;
$docs-space--lg: 30px;

.m-section--docs {
	@include margin-horizontal(-#{round($m-section-gap/2) + 2000});
	padding: 200px 2000px;
	background-color: #e6e6e6;
	background-repeat: repeat-x;
	background-position: center top, center bottom;
	background-image: url(../../img/bg-clouds-top.png), url(../../img/bg-clouds-bottom.png); }


.docs__list {
	@include grid-parent($docs-space);
	@include clear-spacing;
	margin-bottom: $bottom-space--md;

	//+media('<desk-hd')
	//include grid-parent($docs-space--hd)

	//+media('<desk-lg')
	//include grid-parent($docs-space--lg)
	//argin-bottom: $bottom-space--ml

	&:last-child {
		margin-bottom: 0; } }

.docs__item {

	position: relative;
	@include grid-item(3, $docs-space);
	@include font(1.5rem, round-float(18px/15px, 2));
	letter-spacing: normal;

	//+media('<desk-hd')
	//include grid-item-responsive(3, $docs-space--hd)

	//+media('<desk-lg')
	//include grid-item-responsive(3, $docs-space--lg)

	//+media('<1280px')
	//include grid-item-responsive(2, $docs-space--lg)

	//+media('<tab-lg')
	//include grid-item-responsive(3, $docs-space--lg)

	//+media('<tab-sm')
	//include grid-item-responsive(2, $docs-space--lg)

	//+media('<mob-md')
 }	//include grid-item-responsive(1, $docs-space--lg)

.docs__inner {
	display: block;
	height: 100%;
	text-decoration: none;
	color: inherit;
	fill: inherit;
	@include clear;

	svg {
		float: left;
		margin-right: 20px; } }

.docs__text {
	overflow: hidden;
	@include font-alt-bold;
	@include font(12px, 24px);
	text-transform: uppercase;

	span {
		border-bottom: 1px solid $text-color; } }

// hover
.docs__inner:hover {
	color: $link-color;
	fill: $link-color;

	span {
		border-color: $link-color; } }

/* docs end */

/* main slider */
.main-slider {
	position: relative;
	@include margin-horizontal(-$page-layout-space);

	@include media('<desk-sm') {
		@include margin-horizontal(-$page-layout-space--lg); } }

.main-slider__images, .main-slider__titles {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	// before slide load end
	position: relative;
	margin-bottom: 0;

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
		opacity: 1;
		visibility: visible;
 } }		// after slide load end

.main-slider__images__item, .main-slider__titles__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end
	position: relative;
	color: inherit;
	text-decoration: none;
	height: 449px;
	width: 800px;

	@include media('<desk-sm') {
		height: 360px;
		width: 640px; }

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.main-slider__images__item {
	position: relative;
	background-color: $text-color;

	//&::before
	//ontent: ""
	//isplay: block
	//include ratio(800, 449)

	&::after {
		@include pseudo();
		left: 0;
		top: 0;
		@include size(100%);
		background-color: rgba(black, 0.25); }

	img {
		opacity: 0.5;
		transition: opacity $animation-speed;

		position: absolute;
		@include size(100%);
		left: 0;
		top: 0;
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(cover); }

	&.slick-current img {
		opacity: 1; } }

// images
.main-slider__images {
	.slick-arrow {
		background: none;
		border-color: white;

		// hover
		.no-touchevents &:hover {
			background: none; } }

	.slick-prev {
		left: $page-layout-space;

		@include media('<desk-sm') {
			left: $page-layout-space--lg; }

		&:before, &:after {
			border-color: white; } }

	.slick-next {
		right: $page-layout-space;

		@include media('<desk-sm') {
			right: $page-layout-space--lg; }

		&:before, &:after {
			border-color: white; } }

	.slick-dots {
		bottom: 35px;

		li {

			button {
				width: 8px;
				height: 8px;
				background: white;

				&::before {
					background: black; } } } } }

// titles
.main-slider__titles {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	pointer-events: none; }

.main-slider__titles__item {
	text-align: center;

	&::before {
		content: "";
		@include display-inline-block-middle;
		height: 100%; } }

.main-slider__titles__text {
	@include display-inline-block-middle;
	width: 620px;
	max-width: 80%;
	overflow: hidden;
	max-height: 90%;
	@include font-alt-medium;
	@include font(30px, round-float(36/30, 2));
	color: white;
	@include push-auto;

	@include media('<desk-sm') {
		width: 580px;
		font-size: 26px; } }

/* main slider end */

/* leaders */
.leaders {
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.leaders__list {
	@include grid-parent($leaders-gap);
	padding-top: $leaders-gap;
	@include clear-spacing; }

.leaders__item {
	@include grid-item(3, $leaders-gap, round($leaders-gap*2));
	@include font($font-size, $line-height);
	letter-spacing: normal; }

.leaders__inner {
	display: block;
	height: 100%;
	text-decoration: none;
	color: inherit;
	padding: $leaders-space;
	border: 1px solid $border-color;
	text-align: center;
	transition: {
		property: border-color;
		duration: $animation-speed; } }

.leaders__figure {
	position: relative;
	margin-top: -#{$leaders-space*2};
	margin-bottom: 35px;
	background-color: #e6e6e6;
	transition: {
		property: background-color;
		duration: $animation-speed; }

	&::before {
		content: "";
		display: block;
		@include ratio(1, 1); }

	&:last-child {
		margin-bottom: 0; }

	img {
		position: absolute;
		@include size(100%);
		@include position(0);
		display: block;
		max-width: none;
		@include object-fit(cover); } }

//.leaders__content
.leaders__title {
	@include font-alt;
	@include font(24px, round-float(30/24, 2));
	color: $text-color;
	margin-bottom: 0.8em;
	transition: {
		property: color;
		duration: $animation-speed; }

	&:last-child {
		margin-bottom: 0; }

	& > h2, & > h3, & > h4 {
		font: inherit;
		margin-bottom: 0;
		color: inherit; } }

.leaders__text {
	@include font(16px, round-float(20/16, 2));

	& > p, & > ul, & > ol {
		&:last-child {
			margin-bottom: 0; } } }

// hover
.leaders__inner:hover {
	border-color: $link-color;

	.leaders__figure {
		background-color: $link-color; }

	.leaders__title {
		color: $link-color; } }

/* leaders end */

/*btn to top */

.btn-to-top {
	position: fixed;
	right: 0;
	bottom: 0;
	@include size(60px);
	background: #000;
	font-size: 0;
	line-height: 0;
	z-index: 996;
	opacity: 0;
	visibility: hidden;
	transform: translate(100px, 0);
	transition-property: opacity, visibility, transform;
	transition-duration: 0.2s;

	.to-top-show & {
		opacity: 0.18;
		visibility: visible;
		transform: translate(0, 0); }

	.no-touchevents &:hover {
		opacity: 0.45; }

	i {
		position: absolute;
		right: 50%;
		top: 50%;
		height: 10px;
		width: 18px;
		margin: -5px -10px 0 0;
		z-index: 1;

		&:before, &:after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			height: 2px;
			width: 12px;
			border-radius: 5px;
			background: #fff; }

		&:before {
			transform: rotate(-45deg);
			margin: -2px 0 0 -9px; }

		&:after {
			transform: rotate(45deg);
			margin: -2px 0 0 -2px; } } }

/*btn to top end */
