@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* FOOTER */
.footer {
	color: $text-color; }

/* footer gov */
.footer-gov {
	position: relative;
	border-top: 1px solid $border-color-light; }

.gov__list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end
	padding-bottom: 70px;

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 }		// after slide load end

	.slick-list {
		@include margin-horizontal(-#{$grid-space/2}); }

	.slick-prev, .slick-next {
		top: auto;
		bottom: 0;
		margin-top: 0;
		transform: translate(0, 0); }

	.slick-prev {
		left: 0; }

	.slick-next {
		right: 0; }

	.slick-dots {
		bottom: 17px; } }

.gov__item {
	// before slide load
	float: none;
	@include display-inline-block-top;
	// before slide load end
	padding: 0 #{round($grid-space/2)};

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.gov__inner {
	text-decoration: none;
	color: $text-color; }

.gov__figure {
	height: 40px;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(contain, $position: left center); } }

.dov__title {
	text-decoration: underline; }

// hover
.gov__inner:hover {
	.dov__title {
		text-decoration: none; } }

/* footer gov end */

/* footer bottom */
.footer-bottom {
	background-color: #303030;

	&, & a {
		color: white; } }

.footer-bottom__holder {
	position: relative;
	text-align: justify;
	overflow: hidden;
	cursor: default;

	@include clear-spacing;

	&::after {
		content: "1";
		display: inline-block;
		margin-left: 100%;
		height: 1px;
		overflow: hidden; } }

.footer-bottom__col {
	@include display-inline-block-top;
	@include font($font-size, $line-height);
	letter-spacing: normal;
	text-align: center;
	width: 33.28%;

	&:first-child {
		text-align: left; }

	&:last-child {
		text-align: right; }

	ul {
		li {
			em {
				@include display-inline-block-top;
				margin-right: 4px;
				@include font-normal;
				color: $text-color-light; }
			//display: inline-block
			//padding-right: calc(1em - 4px)

			//&::after
			//ontent: "/"
			//isplay: inline-block
			//osition: relative
			//adding-left: 1em
			//olor: $base-color

			//&:last-child
			//::after
 } }			//content: none

	a {
		text-decoration: none;

		&:hover {
			text-decoration: underline; } } }

/* footer bottom end */

/* developer */
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px);

		@if $animation-use {
			transition: {
				property: opacity, transform, filter;
				duration: 0.2s; } } }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px;

		//.no-touchevents a:hover
		//ransform: translate(0, 1px)
		//ilter: blur(2px)
 }		//pacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/* developer end */
/* FOOTER end */
