@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* slick sliders */
// arrows
.slick-arrow {
	@include size(50px);
	display: block;
	position: absolute;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	border: 1px solid #000;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;

	&::before {
		@include pseudo();
		@include size(50px, 0);
		border-top: 2px solid #000;
		opacity: 1;
		margin: -1px 0 0;
		will-change: opacity, transform;
		transform: translate(0, 0);
		transform: translate3d(0, 0, 0);
		transition: {
			property: opacity, transform;
			duration: $animation-speed; } }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(14px);
		transform: rotate(45deg);
		border: 0 solid #000; }

	// hover
	.no-touchevents &:hover {
		opacity: 1;
		//background: #e6e6e6

		&::before {
			opacity: 0;
			//transform: translate(-20px, 0)
 } }			//transform: translate3d(-20px, 0, 0)

	// disabled
	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		background: none;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } } }

.slick-prev {
	left: 0;

	&::before {
		margin-left: 20px; }

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -7px 0 0 -4px; } }

.slick-next {
	right: 0;

	&::before {
		margin-left: -21px; }

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -7px -4px 0 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 4px 10px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 8px;
			height: 8px;
			padding: 0;
			border-radius: 0;
			background: $link-color;
			position: relative;
			transition-property: background, border, box-shadow, transform;
			transition-duration: 0.6s;

			&::before {
				content: '';
				position: absolute;
				left: 1px;
				top: 1px;
				height: 6px;
				width: 6px;
				background: white;
				border-radius: 0;
				opacity: 1;
				//box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20)
				transition-property: transform, opacity;
				transition-duration: $animation-speed; } }

		&.slick-active button::before {
			opacity: 0; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */
