@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* HEADER */
.header {
	position: relative;
	z-index: 5;
	background: url(../../img/bg-header.jpg) no-repeat;
	background-size: cover;

	.panel-is-open & {
		z-index: auto; } }

.header__top {
	position: relative;
	height: $header-top-height;
	border-bottom: 1px solid rgba(#fff, 0.25);
	z-index: 10;

	@include media('<desk-sm') {
		height: $header-top-height--sm; }

	.panel-is-open & {
		z-index: auto; } }

.header__common {
	@include padding-vertical($page-layout-space);
	display: flex;
	justify-content: space-between;
	align-items: center;

	@include media('<desk-sm') {
		@include padding-vertical($page-layout-space--lg); }

	.home-page & {
		flex-wrap: wrap; } }

.logo-box {
	display: flex;
	justify-content: flex-start;
	align-items: center; }

.logo-wrap {
	width: $sidebar-width;
	padding-right: 20px; }

.logo {
	$logo-size: 270px, 75px; // width, height
	$logo-size--lg: 210px; // width
	// ==========
	display: block;
	position: relative;
	width: nth($logo-size, 1);
	max-width: 100%;
	text-indent: -9999px;
	overflow: hidden;
	background: url(../../img/logo.png) no-repeat;
	background-size: contain;
	z-index: 5;

	@include media('<desk-sm') {
		width: nth($logo-size--lg, 1); }

	&::before {
		content: "";
		display: block;
		@include ratio(nth($logo-size, 1), nth($logo-size, 2)); }

	a {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		@include size(100%); } }

.slogan {
	@include font-alt;
	@include font(24px, 29px);
	color: white;
	margin-right: 20px;
	max-width: 480px;

	&.slogan--home-page {
		display: none; }

	.home-page & {
		display: none;
		@include font-alt-bold;
		@include font(52px, round-float(58/52, 2));
		margin-right: 0;
		max-width: 100%;
		padding-right: 15%;

		@include media('<desk-sm') {
			font-size: 38px; }

		@include media('<tab-lg') {
			padding-right: 0; }

		&.slogan--home-page {
			display: block;
			width: 100%;
			margin-top: 30px;

			@include media('<desk-sm') {
				margin-top: 20px; } } }

	a {
		text-decoration: none;
		color: inherit; } }

// panel
.header__panel {
	background-color: $base-color;
	position: fixed;
	height: $header-height;
	@include position(0);
	width: 100%;
	z-index: 50;
	box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.18);
	transform: translate(0, -100%);
	transform: translate3d(0, -100%, 0);
	opacity: 0;
	will-change: transform, opacity;
	transition: {
		property: transform, opacity;
		duration: $animation-speed; }

	.panel-is-open & {
		transform: translate(0, 0);
		transform: translate3d(0, 0, 0);
		opacity: 1; } }

.header__panel__frame {
	@include padding-vertical(5px);
	height: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;

	.home-page & {
		flex-wrap: wrap; } }

.header__panel {
	.logo-wrap {
		width: auto;
		min-width: 0;
		padding-right: 40px; }

	.logo {
		$logo-size: 60px, 60px;
		// ==========
		width: nth($logo-size, 1);
		height: nth($logo-size, 2);
		background-size: auto 100%; }

	.slogan {
		display: none !important; } }

// panel end

/* HEADER end */

/* NAVIGATION */
.nav-wrap {
	height: 100%;

	// panel-is-open
	html:not(.panel-is-open) & {
		transform: translate(0, 0) matrix(1, 0, 0, 1, 0, 0) !important; }

	.panel-is-open & {
		position: fixed;
		left: 0;
		top: 0;
		@include size(100%, 100vh);
		background-color: rgba($bg-color-alt, 1);
		z-index: 1000;
		overflow-x: visible;
		overflow-y: auto; } }

.nav {
	position: relative;
	height: 100%;

	// panel-is-open
	.panel-is-open & {
		height: auto;
		max-width: 640 + $page-layout-space*2;
		@include padding-horizontal($page-layout-space);
		@include push-auto;
		text-align: center;

		&::before {
			content: "";
			@include display-inline-block-middle;
			height: 100vh; } } }

//     
.nav__list {
	height: 100%;
	display: flex;
	justify-content: space-between;
	//align-items: center

	// panel-is-open
	.panel-is-open & {
		@include display-inline-block-middle;
		height: auto;
		width: calc(100% - 5px);
		@include padding-vertical($page-layout-space);
		text-align: left; }

	li {
		position: relative;
		@include font-alt;
		@include font(16px, 20px);
		letter-spacing: 0.05em;

		// panel-is-open
		.panel-is-open & {
			@include font(16px, 24px);
			border: solid #ccc;
			border-width: 1px 0 0;

			@include media('<desk-sm') {
				font-size: 14px; } } }

	.nav__tab {
		.panel-is-open & {
			position: relative; } }

	a {
		display: block;
		color: $text-color;
		text-decoration: none;

		// panel-is-open
		.panel-is-open & {
			display: block;

			// hover
			&:hover {
				color: $link-color; } } } }

//     
.nav__drop {
	position: absolute;
	left: 100%;
	top: 0;
	background-color: white;
	border: 1px solid $border-color;
	padding: 0 20px;
	opacity: 0;
	visibility: hidden;
	z-index: 10;

	.panel-is-close & {
		display: block !important;
		width: 320px; }

	.panel-is-open & {
		border: none;
		padding: 0;
		background-color: transparent; }

	.nav-align--right > & {
		left: auto;
		right: 100%; }

	// no js
	.no-js & {
		position: relative;
		left: auto;
		right: auto;
		top: auto;
		bottom: auto;
		opacity: 1;
		visibility: visible; }

	.panel-is-open & {
		display: none;
		position: relative;
		left: auto;
		right: auto;
		top: auto;
		bottom: auto;
		opacity: 1;
		visibility: visible; }

	li:hover > & {
		opacity: 1;
		visibility: visible; }

	.nav__tab {
		border: solid $border-color-light;
		border-width: 1px 0 0;

		.panel-is-open & {
			border: none; }

		li:first-child > .nav__tab {
			border-width: 1px 0; } }

	a {
		display: block;
		position: relative;
		padding: 15px 20px;
		@include margin-horizontal(-20px);

		.panel-is-open & {
			@include padding-horizontal(0);
			@include margin-horizontal(0); } } }

//    ,    
.nav__list li.has-drop > .nav__tab > a {

	// panel-is-open
	.panel-is-open & {
		padding-right: nth($nav-angle-size, 1) + 20; } }

//      
.nav__list li.current > .nav__tab {
	& {
		a, a:hover {
			color: $base-color; } } }

//       
.nav__list {
	& > li {
		height: 100%;

		// panel-is-open
		.panel-is-open & {
			margin-bottom: -1px;
			font-size: 18px;

			&:last-child {
				border-width: 1px 0; } }

		// <1200px
		@include media('<desk-lg') {
			flex: 1 1 0; }

		& > .nav__tab {
			height: 100%;

			a {
				display: table;
				table-layout: fixed;
				height: 100%;
				color: white;
				text-decoration: none;
				padding: 0 round($nav-gap/2);

				// panel-is-open
				.panel-is-open & {
					display: block;
					padding: 14px 0 16px;
					color: $text-color;

					// hover
					&:hover {
						color: $link-color; } }

				span {
					display: table-cell;
					vertical-align: middle;
					transition: {
						property: opacity;
						duration: $animation-speed; } } } }

		&:first-child {
			& > .nav__tab {
				a {
					padding-left: 0; } } }

		// panel-is-open
		//.panel-is-open &
		//adding-left: 20px

		&:last-child {
			flex-grow: 0;
			flex-basis: auto;

			& > .nav__tab {
				a {
					padding-right: 0; } } }

		& > .nav__drop {
			left: round($nav-gap/2);
			top: 100%;

			// panel-is-open
			.panel-is-open & {
				left: auto;
				top: auto; }

			&::before {
				@include pseudo();
				left: 12px;
				bottom: 100%;
				@include triangle(top, white, 14px, 1);
				margin-bottom: -1px;

				// panel-is-open
				.panel-is-open & {
					content: none; } } }

		&:first-child {
			& > .nav__drop {
				left: 0; } }

		&.nav-align--right {
			& > .nav__drop {
				left: auto;
				right: round($nav-gap/2);

				&::before {
					left: auto;
					right: 12px; } }

			&:last-child > .nav__drop {
				right: 0; } } }

	// hover
	.panel-is-close &:hover {
		& > li {
			& > .nav__tab {
				a {
					span {
						opacity: 0.5; } } } } }

	.panel-is-close & > li:hover {
		& > .nav__tab {
			a {
				span {
					opacity: 1; } } } } }

//  
.nav__angle {
	display: none;

	position: absolute;
	right: -13px;
	top: 0;
	width: nth($nav-angle-size, 1);
	height: 100%;
	//background-color: rgba(black, 0.2)
	//border: 1px solid rgba(black, 0.2)
	z-index: 2;
	cursor: pointer;

	li.has-drop > .region-menu__tab > & {
		display: block; }

	i {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 16px;
		height: 10px;
		pointer-events: none;
		margin: -5px 0 0 -8px;
		transition-property: transform;
		transition-duration: $animation-speed;
		font-size: 0;
		line-height: 0;
		//background-color: coral

		&::before, &::after {
			content: '';
			position: absolute;
			bottom: 0;
			height: 12px;
			width: 2px;
			border-radius: 2px;
			backface-visibility: hidden;
			background-color: $text-color;
			transform-origin: center center;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		&::before {
			right: 50%;
			transform-origin: right bottom;
			transform: rotate(45deg); }

		&::after {
			left: 50%;
			transform-origin: left bottom;
			transform: rotate(-45deg); } }

	// hover
	&:hover {
		i {
			&::before, &::after {
				background-color: $link-color; } } }

	// active
	.is-open > .nav__tab & {
		transform: rotate(-0.5turn); }

	// panel-is-open
	.panel-is-open .has-drop > .nav__tab & {
		display: block; } }

.nav__drop {
	li {
		.panel-is-open & {
			margin-left: 20px; } }

	// hover
	li:hover > .nav__tab a {
		color: $base-color;

		.panel-is-open & {
			color: $text-color;

			&:hover {
				color: $link-color; } } }

	a {
		.panel-is-open & {

			// hover
			&:hover {
				color: $text-color; } } }

	.nav__drop {
		top: 0;
		left: 100%;

		.panel-is-open & {
			left: auto;
			top: auto; }

		a {
			.panel-is-open & {
				@include font-normal;
				//padding-left: 20px
				font-size: 14px; } } } }


/* NAVIGATION end */

