@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* shutter */


// nav shutter

.shutter {
	overflow: hidden; //    
	position: fixed;
	//left: 0
	bottom: 0;
	min-width: 320px;
	background: $bg-color;
	opacity: 0;
	visibility: hidden;
	z-index: 1000;
	left: #{$sidebar-width};
	@include size(100%, calc(100vh - #{$header-height}));

	@include media('<desk-lg') {
		left: 0;
		height: calc(100vh - #{$header-height--sm}); }

	@include media('<desk-sm') {
		left: 0; } }

.shutter__align {
	width: calc(100% + 100px);
	overflow-x: hidden;
	overflow-y: auto;
	height: 100%;

	@include media('<desk-sm') {
		width: 100%; } }

.shutter__holder {
	@include clear;
	padding: nth($shutter-space, 1) $page-layout-space;

	@include media('<desk-hd') {
		padding: nth($shutter-space--hd, 1) $page-layout-space--lg; }

	@include media('<desk-lg') {
		padding: nth($shutter-space--lg, 1) $page-layout-space--lg; }

	@include media('<desk-sm') {
		width: 100%; } }

.shutter__footer {
	border-top: 1px solid $border-color; }

/* shutter content */
.shutter-content {
	float: left;
	width: calc(100% - #{$shutter-aside-width});
	padding-right: $grid-space; }

/* shutter content end */

/* shutter aside */
.shutter-aside {
	float: right;
	width: $shutter-aside-width; }

/* shutter aside end */

/* button shutter close */
.shutter {
	.btn-popup-close {
		display: none;
		position: absolute;
		right: 0;
		top: 0;

		@include media('<tab-lg') {
			display: block; } } }

/* button shutter close end */

/* nav-shutter */
.nav-shutter {
	&, .shutter__holder {
		max-width: $shutter-width;

		@include media('<desk-lg') {
			max-width: $shutter-width--lg; }

		@include media('<desk-sm') {
			max-width: none; } } }

/* nav-shutter end */

/* shutter wide */
.shutter--wide {
	max-width: #{$page-layout-width - $sidebar-width};

	@include media('<1921px') {
		max-width: calc(100%); }

	@include media('<desk-lg') {
		max-width: calc(100%); }

	@include media('<desk-sm') {
		max-width: none; }

	.shutter__align {
		width: 100%; }

	.shutter__holder {
		width: 100%; } }

/* shutter wide end */

/* bag shutter */
.bag-shutter {
	$bag-shutter-width: 470px;
	// ======================
	width: $bag-shutter-width;
	max-width: 100%;
	//left: auto
	//right: 0
	left: $page-layout-width;
	margin-left: -#{$bag-shutter-width};

	@include media('<desk-hd') {
		left: 100%; }

	.shutter__align {
		overflow-y: hidden; }

	.shutter__holder {
		width: #{$bag-shutter-width};
		height: 100%;
		padding: 0;

		&::after {
			content: none; }

		@include media('<tab-lg') {
			width: 100%; } }

	.shutter__row {
		overflow-x: hidden;
		overflow-y: auto;

		&.shutter__footer {
			overflow-y: hidden; } }

	.shutter__cell {
		padding: $grid-space; } }

/* bag shutter end */
/* shutter end */
